import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Camera } from '@ionic-native/camera/ngx';
import { IOptionsPsComplexAmountExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-amount/ps-complex-amount.component.interfaces';
import { IOptionsPsDateDayMonthYearPastExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-day-month-year/ps-date-day-month-year-past/ps-date-day-month-year-past.component.interfaces';
import { IOptionsPsInputAccountNumberExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-account-number/ps-input-account-number.component.interface';
import { IOptionsPsInputNumericExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-numeric/ps-input-numeric.component.interfaces';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { IOptionsPsLookupChequebooksExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-chequebooks/ps-lookup-chequebooks.component.interfaces';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsPsFileUploadComponent, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';



@Component({
  selector: 'cheque-confirmation',
  templateUrl: './cheque-confirmation.page.html',
  styleUrls: ['./cheque-confirmation.page.scss'],
})
export class ChequeConfirmationPage extends OmniBasePage implements OnInit {

  private formGroup = new FormGroup({});
  chequeConfirmationVO: any = {};
  cifInfo;

  public stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'cheque_confirmation_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['cheque_confirmation_step1'],
    group: this.formGroup,
    submitOptions: {
      group: this.formGroup,
      submitServiceUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
    },
    requestObject: this.chequeConfirmationVO
  };

  chequeConfirmationDetailsPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'cheque_confirmation_details_key',
    //iconName: 'document',
    expanded: true
  };

  attachmentPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'attachment_key',
    iconName: '',
    expanded: false,
  };

  chequeBookLookupOptions: IOptionsPsLookupChequebooksExposed = {
    labelKey: 'chequebook_key',
    placeHolder: 'select_chequebook_key',
    group: this.formGroup,
    fcName: 'chequebook',
    requestObject: this.chequeConfirmationVO
  };
  accountNumberOptions: IOptionsPsInputAccountNumberExposed = {
    fcName: 'accountNumber',
    labelKey: 'account_number_key',
    placeHolder: 'enter_account_number_key',
    group: this.formGroup
  };

  chequesNumberOptions: IOptionsPsInputNumericExposed = {
    fcName: 'chequeNumber',
    group: this.formGroup,
    labelKey: 'cheque_number_key',
    placeHolder: 'enter_cheque_number_key',
  };

  securityNumberOptions: IOptionsPsInputAccountNumberExposed = {
    fcName: 'securityNumber',
    labelKey: 'security_number_key',
    placeHolder: 'enter_security_number_key',
    group: this.formGroup
  };

  chequeAmountOptions: IOptionsPsComplexAmountExposed = {
    currenciesOptions: {
      labelKey: 'currency_key',
      placeHolder: 'currency_key',
      group: this.formGroup,
      fcName: 'chequeCurrency'
    },
    amountOptions: {
      labelKey: 'amount_key',
      placeHolder: 'enter_amount_key',
      fcName: 'amount',
      group: this.formGroup,
      decimalPoints: 3
    },
    showLimitLabel: false
  };

  chequeDateOptions: IOptionsPsDateDayMonthYearPastExposed = {
    labelKey: 'cheque_date_key',
    placeHolder: 'cheque_date_key',
    fcName: 'chequeDate',
    group: this.formGroup,
  };

  beneficiaryNameOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'beneficiary_key',
    placeHolder: 'enter_beneficiary_name_key',
    group: this.formGroup,
    fcName: 'beneficiary'
  };


  versoImageUploadOptions: IOptionsPsFileUploadComponent = {
    multiple: false,
    labelKey: 'cheque_verso_image_key',
    fcName: 'selectedFileData_veroImg',
    group: this.formGroup,
    hasOpenCameraBtn: true,
    takePhotoLabelKey: 'cheque_verso_image_key',
    maxImgWidth: 18,
    maxImgHeight: 8.5
  };

  rectoImageUploadOptions: IOptionsPsFileUploadComponent = {
    multiple: false,
    labelKey: 'cheque_recto_image_key',
    fcName: 'selectedFileData_rectImg',
    group: this.formGroup,
    hasOpenCameraBtn: true,
    takePhotoLabelKey: 'cheque_recto_image_key',
    maxImgWidth: 18,
    maxImgHeight: 8.5
  };

  commentOptions: IOptionsPsInputVarcharExposed = {
    group: this.formGroup,
    fcName: 'comment',
    labelKey: 'file_comment_key',
    placeHolder: 'enter_file_comment_key'
  };

  imageBase64Url: string;
  imagedataValue: any;
  fileNamevalue: any;
  acceptFormatExt: string[];

  constructor(private commonService: PsCommonService, private logger: LoggerService, private camera: Camera) {
    super();
  }

  ngOnInit() {
    super.init();
    this.cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.accountNumberOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.chequeAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.commentOptions.fcName], 0);

  }

  onChequeChanged(event) {
    this.commonProv.setValInsideNestedObj(this.accountNumberOptions.fcName, event.additionalRef, this.chequeConfirmationVO);
    this.commonProv.setValInsideNestedObj(this.chequeAmountOptions.currenciesOptions.fcName, event.currency, this.chequeConfirmationVO);
  }
}
